import { Component } from '@angular/core';
import { Platform ,LoadingController} from 'ionic-angular';
import { Network } from '@ionic-native/network';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginPage } from '../pages/login/login';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = LoginPage;
  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen,private network:Network, private loadingCtrl: LoadingController) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.show();
      splashScreen.hide();
      this.checkNetwork();
    });
  }

  /*检测网络，若未连接网络，给出提示*/
  checkNetwork() {
    if(this.network.type === 'unknown') {
      console.log('This is a unknown network, please be careful!');
    } else if(this.network.type === 'none') {
      console.log('none network!');
      let loader = this.loadingCtrl.create({
        content: "当前网络不可用，请检查网络设置！"
      });
      loader.present();
    } else {
      console.log('we got a ' + this.network.type + ' connection, woohoo!');
    }
   }
}



